import {
    v4 as uuidv4
} from "uuid";
import MySocket from "@/models/MySocket";
import {
    SVG
} from "@svgdotjs/svg.js";
import Sockets from "@/values/sockets.js";

export default class MyNode {
    constructor(parent, network, state, allPath, name, comp) {
        const self = this;
        const node = parent.group();
        node.rect(200, 200).attr({ "fill": "#fff", "fill-opacity": 0.7, "stroke-width": 2, "stroke": "#000", "rx": 15 });
        node.draggable();
        const text = node.text(name);
        text.move(80, 10);

        if (comp === "button") {
            const html = node.foreignObject(200, 100);
            html.add(SVG('<button id="button1">Button</button>', true));
            html.move(0, 100);
            const button = document.getElementById("button1");
            button.addEventListener("click", function () {
                network.fresh();
                self.execute(self.entrypointMethod);
            });
        }

        self.name = name;
        self.id = uuidv4();
        self.svg = node;
        self.text = text;
        self.connections = [];
        self.inputs = [];
        self.outputs = [];
        self.allPath = allPath;
        self.state = state;
        self.network = network;
        self.comp = comp;

        node.on("beforedrag", function (e) {
            if (!state.canDrag) {
                e.preventDefault();
            }
        });
        node.on("dragmove", function () {
            node.front();
            self.connections.forEach(function (connection) {
                const pathSvg = self.allPath.get(connection.id);
                let xOffset = 10;
                if (connection.input.type === Sockets.string.name) {
                    xOffset = 40;
                }
                pathSvg.plot("M " + connection.output.svg.cx() + " " + connection.output.svg.cy() + " L " + (connection.input.svg.cx() - xOffset) + " " + connection.input.svg.cy());
            });
        });
    }

    setEntrypoint(entrypointMethod) {
        this.entrypointMethod = entrypointMethod;
    }

    execute(entrypointMethod) {
        const self = this;
        if (self.comp === "button") {
            self.network.next(self.outputs[0].id);
        } else if (self.comp === "var") {
            if (self.network.valueConnections.has(self.inputs[1].id)) {
                console.log("ok I will find value");
            } else {
                const socketValue = self.inputs[1].value;
                entrypointMethod.children.push({
                    type: "assign",
                    left: {
                        items: [
                            {
                                type: "localVar",
                                id: "execution-instance",
                                mid: "entrypoint"
                            },
                            {
                                type: "classVar",
                                id: self.id
                            }
                        ]
                    },
                    right: {
                        expression: {
                            items: [
                                {
                                    type: "value",
                                    value: socketValue
                                }
                            ]
                        }
                    }
                });
                self.network.next(self.outputs[0].id);
            }
        } else if (self.comp === "alert()") {
            const paramItems = [];
            if (self.network.valueConnections.has(self.inputs[1].id)) {
                paramItems.push({ type: "localVar", id: "execution-instance", mid: "entrypoint" });
                paramItems.push({ type: "classVar" });
                self.network.trace(self.inputs[1].id, paramItems[1]);
            } else {
                paramItems.push({ type: "value", value: self.inputs[1].value });
            }

            entrypointMethod.children.push({
                type: "call",
                items: [
                    {
                        type: "method",
                        name: "alert",
                        params: [
                            {
                                items: paramItems
                            }
                        ]
                    }
                ]
            });
            self.network.next(self.outputs[0].id);
        }
    }

    addSocket(type, flow) {
        const self = this;
        var socketX = -10;
        var socketY = 20;
        if (flow === "out") {
            socketX = 190;
            socketY += 40 * self.outputs.length;
        } else {
            socketY += 40 * self.inputs.length;
        }

        const socket = new MySocket(self.svg, type, flow, socketX, socketY, self.state, self);
        if (flow === "out") {
            self.outputs.push(socket);
        } else {
            self.inputs.push(socket);
        }
    }

    addConnection(id, output, input) {
        this.connections.push({ id, output, input });
    }
}