<template>
<div class="page my-border">
    <div id="drawing" class="canvas">
    </div>
    <div class="my-border code-area">
        <span>{{codes}}</span>
    </div>
</div>
</template>

<script>
import {
    ref,
    onMounted
} from "vue";
import {
    SVG
} from "@svgdotjs/svg.js";
import "@svgdotjs/svg.draggable.js";
import {
    v4 as uuidv4
} from "uuid";
import MyNode from "@/models/MyNode.js";
import Sockets from "@/values/sockets.js";
import {
    translateJS
} from "@/logics/interpreter_js.js"
import U from "@/logics/utils.js";

export default {
    setup() {
        const codes = ref("");
        const allPath = new Map();
        const network = {};
        const variableMaps = [new Map(), new Map()];
        const codeTree = {
            type: "class",
            id: "class",
            name: "Test",
            children: []
        };
        const entrypoint = {
            type: "entrypoint",
            id: "entrypoint",
            children: [{
                type: "new",
                id: "execution-instance",
                class: "Test",
                name: "classInstance1"
            }]
        };
        const entry = new Map();
        entry.set("execution-instance", entrypoint.children[0]);
        variableMaps[1].set("entrypoint", entry);

        network.execConnections = new Map(); //out_socket_id -> in_socket
        network.valueConnections = new Map(); //in_socket_id -> out_socket
        network.nodes = []; //all added chips
        network.next = function (outSocketId) {
            const nextInSocket = this.execConnections.get(outSocketId);
            if (nextInSocket) {
                nextInSocket.node.execute(entrypoint);
            } else {
                const codeText = [""];
                translateJS(codeTree, 1, codeText, variableMaps, null);
                console.log(codeText[0]);
                //javascript has entrypoint at outside of class
                const entryText = [""];
                translateJS(entrypoint, 1, entryText, variableMaps, "entrypoint");
                console.log(entryText[0]);
                codes.value = codeText[0] + entryText[0];
                eval(codes.value);
            }
        };
        network.trace = function (inSocketId, item) {
            const lastOutSocket = this.valueConnections.get(inSocketId);
            if (lastOutSocket.node.comp === "var") {
                item.id = lastOutSocket.node.id;
            } else {
                //continue trace
            }
        };
        network.fresh = function () {
            entrypoint.children.splice(0, entrypoint.children.length);
            entrypoint.children.push({
                type: "new",
                id: "execution-instance",
                class: "Test",
                name: "classInstance1"
            });
        };
        network.scanNodes = function () {
            //make default constructor
            const constructor = {
                type: "method",
                id: uuidv4(),
                name: "constructor",
                return: {
                    class: ""
                },
                params: [],
                children: []
            };
            //find variable chips
            network.nodes.forEach(function (node) {
                if (node.comp === "var") {
                    //add class variable
                    const right = {
                        expression: {
                            items: [{
                                type: "value",
                                value: "\"\""
                            }]
                        }
                    }
                    codeTree.children.push({
                        type: "init",
                        id: node.id,
                        left: {
                            class: "String",
                            name: "myStringVar1"
                        },
                        right
                    });
                    constructor.children.push({
                        type: "assign",
                        left: {
                            items: [{
                                    type: "instance"
                                },
                                {
                                    type: "classVar",
                                    id: node.id
                                }
                            ]
                        },
                        right
                    });
                }
            });
            codeTree.children.push(constructor);
            U.parseTreeNodeIds(codeTree, variableMaps, null);
            console.log(variableMaps);
        };
        const state = {
            makingConnection: false,
            startSocket: null,
            endSocket: null,
            canDrag: true,
            startNode: null,
            endNode: null
        };

        onMounted(function () {
            const draw = SVG().addTo("#drawing").size("80vw", "100vh");
            const drawingPath = draw.path("M 0 0 L 0 0");
            drawingPath.stroke({
                color: "#d61c00",
                width: 5,
                linecap: "round",
                linejoin: "round"
            });
            drawingPath.hide();
            //const star = draw.svg('<svg height="210" width="500"> <polygon points="100,10 40,198 190,78 10,78 160,198" style="fill:lime;stroke:purple;stroke-width:5;fill-rule:evenodd;"/></svg>');
            //star.move(200, 200);

            draw.on("mouseup", function () {
                if (state.makingConnection) {
                    if (!state.endSocket) {
                        drawingPath.hide();
                    } else {
                        drawingPath.hide();
                        if (state.startNode === state.endNode) {
                            alert("Cannot make connection on the same node!");
                        } else if (network.execConnections.has(state.startSocket.id)) {
                            alert("Output socket can only have 1 connection!");
                        } else if (network.execConnections.get(state.startSocket.id) === state.endSocket.id) {
                            alert("Cannot make duplicate connection!");
                        } else if (state.startSocket.type !== state.endSocket.type) {
                            alert("Not same type!");
                        } else {
                            console.log("make connection!");
                            const pathId = uuidv4();
                            let xOffset = 10;
                            if (state.endSocket.type === Sockets.string.name) {
                                xOffset = 40;
                            }
                            const path = draw.path("M " + state.startSocket.svg.cx() + " " + state.startSocket.svg.cy() + " L " + (state.endSocket.svg.cx() - xOffset) + " " + state.endSocket.svg.cy());
                            path.stroke({
                                color: "#a63c01",
                                width: 5,
                                linecap: "round",
                                linejoin: "round"
                            });
                            allPath.set(pathId, path);

                            state.startNode.addConnection(pathId, state.startSocket, state.endSocket);
                            state.endNode.addConnection(pathId, state.startSocket, state.endSocket);
                            state.startNode.svg.front();
                            state.endNode.svg.front();
                            if (state.startSocket.type === Sockets.exec.name) {
                                network.execConnections.set(state.startSocket.id, state.endSocket);
                            } else {
                                network.valueConnections.set(state.endSocket.id, state.startSocket);
                            }
                            console.log(network);
                        }
                    }
                }
                //Clear everything
                state.makingConnection = false;
                state.startSocket = null;
                state.endSocket = null;
                state.startNode = null;
                state.endNode = null;
            });
            draw.on("mousemove", function (e) {
                if (state.makingConnection) {
                    const startX = state.startSocket.svg.cx();
                    const startY = state.startSocket.svg.cy();
                    const endX = e.offsetX;
                    const endY = e.offsetY;
                    drawingPath.plot("M " + startX + " " + startY + " L " + endX + " " + endY);
                    drawingPath.show();
                }
            });

            const node1 = new MyNode(draw, network, state, allPath, "Button v2", "button");
            node1.addSocket(Sockets.exec.name, "out");
            node1.addSocket(Sockets.int.name, "out");

            const node2 = new MyNode(draw, network, state, allPath, "myStringVar1", "var");
            node2.addSocket(Sockets.exec.name, "in");
            node2.addSocket(Sockets.exec.name, "out");
            node2.addSocket(Sockets.string.name, "out");
            node2.addSocket(Sockets.string.name, "in");

            const node3 = new MyNode(draw, network, state, allPath, "Alert()", "alert()");
            node3.addSocket(Sockets.exec.name, "in");
            node3.addSocket(Sockets.exec.name, "out");
            node3.addSocket(Sockets.string.name, "in");

            network.nodes.push(node1);
            network.nodes.push(node2);
            network.nodes.push(node3);

            network.scanNodes();
        });

        return {
            codes
        };
    }
}
</script>

<style scoped>
.canvas {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.my-border {
    border-style: solid;
    border-color: #fff;
}

.page {
    display: flex;
    height: 100vh;
}

.code-area {
    width: 20vw;
    color: #fff;
    text-align: left;
    padding: 20px;
}
</style>
