import U from "@/logics/utils.js";

export function translateJS(node, level, display, temp, parentId) {
    //node -> JSON node tree
    //level -> 1
    //display -> [""]
    //temp[0] -> classVars:Map [classVarId, classVar]
    //temp[1] -> localVars:Map [methodId, [localVarId, localVar]]
    //parentId -> might be undefined
    let indent = U.getIndent(level);

    if (node.type === "class") {
        display[0] += indent + " " + node.type + " " + node.name + " {\n";
    } else if (node.type === "method") {
        //Javascript does not have return type on function
        display[0] += indent + " " + node.name + "(";
        if (node.params) {
            node.params.forEach(function (param, index) {
                if (index > 0) {
                    display[0] += ", "
                }
                display[0] += param.class + " " + param.name;
            });
        }
        display[0] += ") {\n";

    } else if (node.type === "init") {
        //Javascript does not need to declare class variables at class level
        if (parentId !== "class") {
            display[0] += indent + " " + node.left.class + " " + node.left.name;
            display[0] += " = " + U.makeExpression(node.right.expression, "this", temp, parentId) + ";\n";
        }

    } else if (node.type === "assign") {
        //assign left is always a getter!
        display[0] += indent + " " + U.makeGetterPhrase(node.left.items, "this", temp, parentId);
        display[0] += " = " + U.makeExpression(node.right.expression, "this", temp, parentId) + ";\n";

    } else if (node.type === "entrypoint") {
        //javascript should do nothing

    } else if (node.type === "new") {
        display[0] += indent + " const " + node.name + " = new " + node.class + "();\n";

    } else if (node.type === "call") {
        let text = "";
        node.items.forEach(function (item) {
            if (item.type === "method") {
                text += item.name;
                text += "("
                item.params.forEach(function (param, index, array) {
                    param.items.forEach(function (paramItem, index, array) {
                        if (paramItem.type === "localVar") {
                            text += temp[1].get(paramItem.mid).get(paramItem.id).name
                        } else if (paramItem.type === "classVar") {
                            text += temp[0].get(paramItem.id).left.name
                        } else if (paramItem.type === "value") {
                            text += paramItem.value
                        }
                        if (index < array.length - 1) {
                            text += ".";
                        }
                    });
                    if (index < array.length - 1) {
                        text += ", ";
                    }
                });
                text += ");"
            }
        });
        display[0] += indent + text;

    } else if (node.type === "if") {
        display[0] += indent + "if " + U.makeExpression(node.expression, "this", temp, parentId) + " {\n";
    } else {
        return;
    }

    if (!node.children) {
        if (node.type === "class" || node.type === "method" || node.type === "if") {
            display[0] += indent + " }\n";
        }
        return;
    }
    node.children.forEach(function (child) {
        translateJS(child, level + 1, display, temp, node.id);
    });
    if (node.type === "if") {
        display[0] += indent + "}\n";
    } if (node.type === "entrypoint") {
        display[0] += "\n";
    } else {
        display[0] += indent + " }\n";
    }
}