export default {
    parseTreeNodeIds(node, temp, parent) {
        const self = this;
        //temp[0] -> classVars:Map [classVarId, classVar]
        //temp[1] -> localVars:Map [methodId, [localVarId, localVar]]
        if (node.type === "class") {
            console.log("class not need id");
        } else if (node.type === "init") {
            if (parent.type === "class") {
                temp[0].set(node.id, node);
            } else if (parent.type === "method") {
                let localVars = temp[1].get(node.id);
                if (!localVars) {
                    localVars = new Map();
                    temp[1].set(node.id, localVars);
                }
                localVars.set(node.id, node);
            }
        } else if (node.type === "method") {
            let localVars = temp[1].get(node.id);
            if (!localVars) {
                localVars = new Map();
                temp[1].set(node.id, localVars);
            }
            if (node.params) {
                node.params.forEach(function (param) {
                    localVars.set(param.id, param);
                });
            }
        }
        if (!node.children) {
            return;
        }
        node.children.forEach(function (child) {
            self.parseTreeNodeIds(child, temp, node);
        });
    },
    getIndent(count) {
        if (count < 1) {
            return "";
        }
        let indent = "";
        while (count > 0) {
            indent += "\t";
            count -= 1;
        }
        return indent;
    },
    makeGetterPhrase(items, instanceName, variableMaps, parentId) {
        let line = "";
        items.forEach(function (item, index) {
            if (item.type === "instance") {
                line += instanceName;
            }
            else if (item.type === "classVar") {
                let classVar = variableMaps[0].get(item.id);
                line += classVar.left.name;
            }
            else if (item.type === "localVar") {
                let localVar = variableMaps[1].get(parentId).get(item.id);
                line += localVar.name;
            }
            else if (item.type === "value") {
                line += item.value;
            }

            if (index + 1 !== items.length) {
                line += "."
            }
        });
        return line;
    },
    makeExpression(operation, instanceName, variableMaps, methodId) {
        const self = this;
        if (!operation.operator) {
            return self.makeGetterPhrase(operation.items, instanceName, variableMaps, methodId);
        }
        return "(" + self.makeExpression(operation.left, instanceName, variableMaps, methodId) + " " + operation.operator + " " + self.makeExpression(operation.right, instanceName, variableMaps, methodId) + ")";
    },
    getClassName(words) {
        let formattedName = "";
        if (words.length > 0) {
            let count = 0;
            while (count < words.length) {
                let word = words[count].toLowerCase();
                formattedName += word.charAt(0).toUpperCase() + word.slice(1);
                count += 1;
            }
        }
        return formattedName;
    },
    getReferenceName(words) {
        let formattedName = words[0].toLowerCase();
        if (words.length > 0) {
            let count = 1;
            while (count < words.length) {
                let word = words[count].toLowerCase();
                formattedName += word.charAt(0).toUpperCase() + word.slice(1);
                count += 1;
            }
        }
        return formattedName;
    }
}